body {
  margin: 0;
  font-family: Roboto, serif;
}

:root {
  --header-height: 60px;
  --hover-color: #00acdc;
}

.logo {
  width: 40px;
  height: 40px;
}

.header {
  box-sizing: border-box;
  height: var(--header-height);
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav {
  align-items: center;
  margin-left: auto;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: var(--header-height);
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  text-decoration: none;
  display: flex;
  position: relative;
}

@media (width: 1024px) {
  .header {
    width: 1024px;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link:hover, .is-active {
  color: var(--hover-color);
}

.is-active:after {
  border-bottom: 4px var(--hover-color) solid;
  content: "";
  border-radius: 8px;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
}
